html,body {
	width: 100%;
	height: 100%;
	margin: 0px;
	padding: 0px;
	overflow-x: hidden;
}

#root {
	font-family: vazir;
	direction: rtl;
	background-color: #f7fff7;
}

nav {
	padding-left: 4rem!important;
	padding-right: 4rem!important;
}

.red_strike {
	color: red;
}
.red_strike span {
	color: gray;
}

.w-95{
	width: 95% !important;
}

.no-select {
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

.balanced-text {
	height: 90%;
	line-height: 1.5;
	vertical-align: middle;
	font-size: 0.800rem;
	font-weight: 400;
}


.carousel .carousel-indicators li {
	background-color: #fff;
	background-color: rgba(70, 70, 70, 0.25);
}

.force-ltr {
	direction: ltr !important;
}

.carousel-indicators{
	position: relative !important;
}

.carousel .carousel-indicators .active {
	background-color: #444;
}

.navbar-brand {
	width: 3rem;
}

.nav-exit{
	color: red !important;
}

.top-bg {
	background-color: #FF6B6B;
	min-height: 10rem;
	padding: 1.5rem;
}

.search-bar {
	margin-top: -1.5rem;
	min-height: 3rem;
}

.top-home-bg {
	background:linear-gradient(0deg, rgba(255, 130, 130, 0.81), rgba(255, 130, 130, 0.81)), url("Cover Photo.jpg");
	background-size:cover;
	background-repeat:no-repeat;
	background-position:center center;
	min-height: 15rem;
	padding: 1.5rem 5rem;
}


.restaurant-logo {
	margin-top: -7.5rem;
	margin-bottom: 1rem;
	width: 15rem;
	border-color: white;
	border-style: solid;
	border-radius: 2rem!important;
	border-width: 0.5rem;
}

.border-strong {
	border-width:0.1rem !important;
}

.loghme-logo {
	margin-bottom: 1rem;
	width: 10rem;
}

.text-shadow{
	text-shadow: black 0.1rem 0.1rem 0.1rem;
}

.underlined {
	text-decoration: underline;
	text-decoration-color: gray;
	text-underline-position: under;
}

.star-logo {
  width: 0.75rem;
  height: 0.75rem;
}

.cart-logo {
	width: 2rem;
	height: 2rem;
	cursor: pointer;
}

.minus-logo {
  color: red;
}

.plus-logo {
	color: green;
}

.cart-logo:before {
	font-size: 1.5rem !important;
	margin: 0 !important;
}

#lblCartCount {
	font-size: 12px;
	vertical-align: top;
	position:relative;
	left:2.2rem;
}

.profile-logo {
	font-size: 4rem !important;
	vertical-align: middle;
}

.order-list {
	border: 0.12rem dashed #a0a0a0;
	border-radius: 2rem;
	padding: 1rem;
}

.user-info {
	border: 0.12rem solid #a0a0a0;
	border-radius: 2rem;
	padding: 1rem;
	background-color: white;
}

.order-item {
	margin: 0.2rem;
	border-bottom: 0.12rem solid #a0a0a0;
}

.contact-info {
	font-size: 1.5rem;
}

.page-footer {
	box-shadow: 3rem 3rem 3rem 3rem rgba(0,0,0,.175)!important;
	-moz-box-shadow: 3rem 3rem 3rem 3rem rgba(0,0,0,.175)!important;
	-webkit-box-shadow: 3rem 3rem 3rem 3rem rgba(0,0,0,.175)!important;
	background-color: #1a535c;
}

.custom-info-badge {
	color: #17a2b8;
	background-color: #d1fffc;
	padding: 0.5rem;
	border-radius: 0.5rem;
}

.timer-badge {
	color: black;
	background-color: #d1fffc;
	padding: 0.5rem;
	border-color: black;
	border-radius: 0.5rem;
}

.custom-success-badge {
	color: #4d8a4d;
	background-color: #cbffca;
	padding: 0.5rem;
	border-radius: 0.5rem;
}

.custom-warning-button {
	color: #000;
	background-color: #fee35b;
	padding: 0.5rem;
	border-radius: 0.5rem;
}

.teb-menu {
	margin-top: -2rem;
}

.tab-item {
	background-color: white;
	color: black;
	padding: 1rem;
}

.tab-item.selected{
	color: white !important;
	background-color: #FF6B6B !important;
}

.user-nav-menu-border {
	border-right: 0.1rem dashed #969696;
}

.dashed-bottom{
	border-bottom: 0.17rem dashed #a0a0a0;
}

@media (min-width: 768px) {
	.restaurant-border {
		border-right: 0.17rem dashed #a0a0a0;
	}
}