@font-face {
  font-family: "Flaticon";
  src: url(/static/media/Flaticon.4dbc0d68.eot);
  src: url(/static/media/Flaticon.4dbc0d68.eot?#iefix) format("embedded-opentype"),
       url(/static/media/Flaticon.5c19cf8b.woff2) format("woff2"),
       url(/static/media/Flaticon.0bc3200e.woff) format("woff"),
       url(/static/media/Flaticon.491bf3d9.ttf) format("truetype"),
       url(/static/media/Flaticon.7df4aca5.svg#Flaticon) format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: "Flaticon";
    src: url(/static/media/Flaticon.7df4aca5.svg#Flaticon) format("svg");
  }
}

[class^="flaticon-"]:before, [class*=" flaticon-"]:before,
[class^="flaticon-"]:after, [class*=" flaticon-"]:after {   
  font-family: Flaticon;
font-style: normal;
}

.flaticon-plus:before { content: "\f100"; }
.flaticon-minus:before { content: "\f101"; }
.flaticon-account:before { content: "\f102"; }
.flaticon-smart-cart:before { content: "\f103"; }
.flaticon-card:before { content: "\f104"; }
.flaticon-mail:before { content: "\f105"; }
.flaticon-phone:before { content: "\f106"; }
